.overlay {
    visibility: hidden; 
     overflow: hidden;
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100vh;
    z-index: 1;
    background-color: rgba(0, 0, 0, 0.2);
    opacity: 0;
    transition: opacity 0.1s ease-in-out, visibility 0.2s ease-in-out,;
  }
  .overlayVisible{
    visibility: visible ; 
    opacity: 1; 
    .drawer{
        transform: translateX(0);
    }
  }
  .drawer {
    padding: 20px;
    position: absolute;
    width: 420px;
    right: 0;
    height: 100vh;
    background: #ffffff;
    box-shadow: -10px 4px 24px rgba(0, 0, 0, 0.1);
    display: flex;
    flex-direction: column;
    transform: translateX(100%);
    transition: transform 0.3s ease-in-out;
    h2 {
      font-size: 24pt;
      font-weight: 400;
      font-family: "Pacifico", cursive;
      padding: 0 10px 10px;
      margin: 0;
    }
  }
  .cartTotalBlock {
    li {
      display: flex;
      align-items: flex-end;
      div {
        
        margin: 6px 4px;
        flex: 1;
        height: 1px;
        border-bottom: 1px dashed #a8a8a8;
      }
    }
  }
  